@font-face
  font-family: 'Inter'
  src: url("../fonts/Inter-VariableFont_slnt,wght.ttf")
  font-weight: 100 1000
  font-stretch: 25% 151%
html
  font-family: Inter, sans-serif
  font-size: 16px
  font-weight: 500
  box-sizing: border-box
*, *:before, *:after
  box-sizing: inherit

ul[class], ol[class]
  padding: 0
  list-style: none
h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd
  margin: 0
input,button,textarea,select
  font: inherit
img
  max-width: 100%
  display: block
body
  margin: 0
  padding: 0
  min-height: 100vh
  width: 100vw
.form-wrap, .err-wrap, .chat-wrap, .profile-wrap
  min-height: 100vh
  width: 100vw
  display: flex
  justify-content: center
  align-items: center
.input
  border: none
  border-bottom: 1px solid #3369F3
  appearance: none
  font-size: 0.8125rem
  padding: 0.43rem 0
  background: none
.btn
  display: block
  margin: 0 auto
  cursor: pointer
  appearance: none
  border: 0
.link
  text-align: center
  text-decoration: none
  font-size: 0.6875rem
  color: #3369F3



